:root {
  --dark2: black;
  --light2: white;
  --accent: #401365;
  --highlight: #F8D911;
  --schedule-color-1: var(--accent);
  --schedule-color-2: purple;
  --primary-font: 'Lexend Mega', sans-serif;
  --secondary-font: 'Nunito Sans' sans-serif;
}

body {
  width: 100%;
  font-family: var(--secondary-font);
}

.page {
  min-height: 100vh;
  display: flex;
  align-items: center;
}

.box {
  padding: 3rem;
  background-color: white;
  box-shadow: 0 4px 8px 0 var(--accent), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 2px;
}

.title {
  font-family: var(--primary-font);
}

.button {
  display: flex;
  justify-content: center;
  padding-top: 20px;
  margin: auto;
}

.button-link {
  background-color: var(--accent);
  color: var(--light);
  font-size: 1.3rem;
  padding: 8px 30px;
  text-transform: uppercase;
  border-radius: 4px;
  transition-duration: 0.3s;
}

.button-link:hover {
  background: var(--schedule-color-2);
  text-decoration: none;
  color: var(--light2);
}

.fade-in {
  animation: fadeIn ease 1s;
  -webkit-animation: fadeIn ease 1s;
  -moz-animation: fadeIn ease 1s;
  -o-animation: fadeIn ease 1s;
  -ms-animation: fadeIn ease 1s;
}

@media (max-width: 1100px) {
  #about-us .box {
    width: 90vw;
  }
}

@media (max-width: 768px) {
  .box {
    padding: 6vw;
  }

  .title {
    font-size: 8vw;
  }

  #subtitle {
    font-size: 4vw;
  }

  .button-link {
    font-size: 2.5vw;
  }

  #hackathon h4 {
    font-size: 3vw;
  }

  .vertical-timeline-element-title {
    font-size: 20px;
  }

  .vertical-timeline-element-content p {
    font-size: 15px;
  }

  #partners h4 {
    font-size: 3vw;
    padding: 2vw;
    text-align: center;
  }
}

@keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-moz-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-webkit-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-o-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-ms-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}
