#about-us {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#about-us .box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 70vw;
}

#about-us .title {
    font-size: 2.5rem;
}

#about-us ul {
    padding-top: 2vw;
    font-size: 1.2rem;
}

#about-us li {
    padding: 1vw;
}
