#hackathon {
    display: flex;
    background-color: var(--accent);
    color: var(--light2);
}

#hackathon .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

#hackathon .date {
    font-family: var(--primary-font);
    text-transform: uppercase;
}

#hackathon h4 {
    font-size: 1.5rem;
}

#hackathon .button {
    justify-content: flex-start;
    margin: 0;
}

#hackathon .button-link:not(:hover) {
    background-color: var(--light2);
    color: var(--accent);
}
