#NavBar {
    display: flex;
    top: 0;
    width: 100%;
    height: 10vh;
    align-items: center;
    justify-content: space-between;
}

#logo {
    height: 70px;
    float: left;
    padding-left: 20px;
}

.menuItems {
    display: flex;
    padding: 10px;
}

.menuItem {
    padding: 10px;
    justify-content: center;
    color: var(--light2);
    vertical-align: middle;
}

.menuItem:hover {
    background-color: grey;
    color: var(--light2);
    text-decoration: none;
    border-radius: 4px;
    transition-duration: 0.3s;
}

#menu {
    width: 100%;
    padding-right: 50px;
}

#menu-items{
    float: right;
}

#floating-menu {
    background-color: var(--dark2);
    /*padding: 15px 50px;*/
    position: fixed;
    /*top: 0;*/
    z-index: 4;
}

#mobile-menu-hamburger {
    color: var(--light2);
    padding: 20px;
    justify-content: center;
    vertical-align: middle;
    font-size: 20px;
}

@media (min-width: 1100px) {
    #mobile-menu-hamburger {
        display: none;
    }
}

@media (max-width: 1100px) {
    #NavBar:not(.hidden) {
        position: relative;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background: var(--dark2);
        z-index: 10;
        width: 300px;
        height: 100vh;
        float: right;
    }

    #NavBar:not(.hidden) + #first-page {
        height: 100vh;
    }

    .logo:not(.hidden) {
        display: none;
    }

    .menuItems.hidden {
        display: none;
    }

    .menuItems {
        flex-direction: column;
    }
}
